
import { defineComponent } from "vue";
import HtsTemplate from "@/apps/HTS/views/reports/templates/DataTableTemplate.vue";
import { ReportTemplateParams } from "@/apps/HTS/types/ReportTypes";
import { HtsReportService } from "@/apps/HTS/services/hts_report_service";
import { sort } from 'fast-sort';
import table from "@/components/DataViews/tables/ReportDataTable"
import { Patientservice } from "@/services/patient_service";
import { modalController } from "@ionic/vue";
import router from "@/router";

export default defineComponent({
  components: { HtsTemplate },
  setup() {
    const report = new HtsReportService("HTS INDEX");
    const params: ReportTemplateParams = {
      logo: "/login-logos/PEPFAR.png",
      title: "HTS_INDEX",
      prefix: 'Pepfar',
      drillDownLabelPattern: (label: string, data: any) => `${data.age_group} ${data.gender} ${label}`,
      initialSort: (data: any) => sort(data).asc((d: any) => d.gender),
      maxDate: HtsReportService.getSessionDate(),
      columns: [
        [
          {
            label: '',
            exportable: false,
            colSpan: 7
          },
          {
            label: "Facility",
            exportable: false,
            colSpan: 4
          },
          {
            label: "Community",
            exportable: false,
            colSpan: 4
          },
        ],
        [
          {
            label: "Age group",
            value: (d: any) => d.age_group
          },
          {
            label: "Gender",
            value: (d: any) => d.gender,
          },
          {
            label: "Index Clients",
            columnType: 'drilldown',
            value: (d: any) => d.index_clients
          },
          {
            label: "Offered Index",
            columnType: 'drilldown',
            value: (d: any) => d.offered_index
          },
          {
            label: "Accepted AIT",
            columnType: 'drilldown',
            value: (d: any) => d.accepted_ait
          },
          {
            label: "Contacted Elicited",
            columnType: 'customDrillDown',
            value: (d: any) => d.contacted_elicited.map((d: any) => d.patient),
            drillParams: (d: any, reportData: any) => {
              return {
                title: 'Contacted Elicited',
                columns: [
                  [
                    table.thTxt('National ID'),
                    table.thTxt('First name'),
                    table.thTxt('Last name'),
                    table.thTxt('Birthdate'),
                    table.thTxt('Contacts'),
                    table.thTxt('Action')
                  ]
                ],
                data: d.contacted_elicited,
                generator: async (rows: Array<any>) => {
                  const row = []
                  for(const data of rows) {
                    const id = `${data.patient}_contact_elicited`
                    if (reportData.drill[id]) {
                      row.push(reportData.drill[id])
                      continue
                    }
                    try {
                      const patient = new Patientservice((await Patientservice.findByID(data.patient))) 
                      reportData.drill[id] = [
                        table.td(patient.getNationalID()),
                        table.td(patient.getGivenName()),
                        table.td(patient.getFamilyName()),
                        table.tdDate(`${patient.getBirthdate()}`),
                        table.td(data.contacts),
                        table.tdBtn('Show', () => {
                          router.push({ path: `/patient/dashboard/${id}`})
                          modalController.dismiss({})
                        })
                      ]
                      row.push(reportData.drill[id])
                    } catch (e) {
                      console.error(e)
                    }
                  }
                  return row
                }
              }
            }
          },
          {
            label: "New positives",
            columnType: 'drilldown',
            exportLabel: () => "New positives (Facility)",
            value: (d: any) => d.facility.new_positives
          },
          {
            label: "New Negatives",
            columnType: 'drilldown',
            exportLabel: () => "New Negatives (Facility)",
            value: (d: any) => d.facility.new_negatives
          },
          {
            label: "Known positives",
            columnType: 'drilldown',
            exportLabel: () => "Known positives (Facility)",
            value: (d: any) => d.facility.known_positives
          },
          {
            label: "Documented Negatives",
            columnType: 'drilldown',
            exportLabel: () => "Documented Negatives (Facility)",
            value: (d: any) => d.facility.documented_negatives
          },
          {
            label: "New positives",
            columnType: 'drilldown',
            exportLabel: () => "New positives (Community)",
            value: (d: any) => d.community.new_positives
          },
          {
            label: "New Negatives",
            columnType: 'drilldown',
            exportLabel: () => "New Negatives (Community)",
            value: (d: any) => d.community.new_negatives
          },
          {
            label: "Known positives",
            columnType: 'drilldown',
            exportLabel: () => "Known positives (Community)",
            value: (d: any) => d.community.known_positives
          },
          {
            label: "Documented Negatives",
            columnType: 'drilldown',
            exportLabel: () => "Documented Negatives (Community)",
            value: (d: any) => d.community.documented_negatives
          }
        ]
      ],
      hasPeriodSelection: true,
      data: (r: any, f: any, computedData: any) => {
        report.startDate = computedData.start_date;
        report.endDate = computedData.end_date;
        return report.requestReport();
      }
    }
    return { params }
  }
})
